<template>
  <div class="row profile" style="width:100%;">
    <div class="col-lg-3 col-md-3 col-sm-12 reset-col content-side-bar">
      <div class="profile-sidebar" style="border-top: 3px solid #03A9F4">
        <!-- SIDEBAR USERPIC -->
        <div class="profile-userpic">
          <img :src="photo" class="rounded mx-auto d-block" alt="User Photo" style="max-width:90px; max-height:90px;">
        </div>
        <!-- END SIDEBAR USERPIC -->
        <!-- SIDEBAR USER TITLE -->
        <div class="profile-usertitle">
          <div class="profile-usertitle-name">
            {{displayName}}
          </div>
        </div>
        <!-- END SIDEBAR USER TITLE -->
        <!-- SIDEBAR BUTTONS -->
        <!-- END SIDEBAR BUTTONS -->
        <!-- SIDEBAR MENU -->
        <div class="profile-usermenu">
          <ul class="nav flex-column">
            <li class="nav-item">
              <router-link class="nav-link" to="/user/bargains"><i class="fa fa-home" aria-hidden="true"></i>My Bargains</router-link>
            </li>
            <li class="nav-item">
              <router-link class="nav-link" to="/user/profile"><i class="fa fa-user" aria-hidden="true"></i>Account Settings</router-link>
            </li>
          </ul>
        </div>
        <!-- END MENU -->
      </div>
    </div>

    <!--<div class="col-lg-2 col-md-12 col-sm-12 reset-col content-side-bar">
      <div class="ads-one" style="margin-top:5px;">
        <div class="card" style="height:533px; border-top: 3px solid #29B6F6;">
          <ul class="nav flex-column nav-justified">
            <li class="nav-item">
              <router-link class="nav-link" to="profile">Profile</router-link>
            </li>
            <li class="nav-item">
              <router-link class="nav-link" to="bargains">My Bargain</router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>-->
    <div class="col-lg-9 col-md-9 reset-col user-content-bar">
      <router-view></router-view>
    </div>
  </div>
</template>

<style>


  @media (min-width: 0) {
    div.user-content-bar.col-lg-9 {
      padding-left: 0;
      margin-top: 10px;
    }
  }
  @media (min-width: 768px) {
    div.user-content-bar.col-lg-9 {
      padding-left: 15px;
      margin-top: 0;
    }
    div.content-side-bar.col-lg-3 {
      -webkit-box-flex: 0;
      -webkit-flex: 0 0 20%;
      -ms-flex: 0 0 20%;
      flex: 0 0 20%;
    }
  }

@media (min-width: 992px) {
  div.content-side-bar {
    max-width: 250px;
  }
  div.profile div.col-lg-10 {
    flex: 0 0 73.333333%;
    max-width: 73.333333%;
  }
}
@media (min-width: 1200px) {
  div.user-content-bar.col-lg-9 {
    flex: 0 0 80%;
    max-width: 80%;
  }
}

@media (min-width: 1425px) {
  div.user-content-bar.col-lg-9 {
    flex: 0 0 80%;
    max-width: 80%;
  }
}



.profile {
  margin: 20px 0;
}

/* Profile sidebar */
.profile-sidebar {
  padding: 20px 0 10px 0;
  background: #fff;
}

.profile-userpic img {
  float: none;
  margin: 0 auto;
  width: 50%;
  height: 50%;
  -webkit-border-radius: 50% !important;
  -moz-border-radius: 50% !important;
  border-radius: 50% !important;
}

.profile-usertitle {
  text-align: center;
  margin-top: 20px;
}

.profile-usertitle-name {
  color: #5a7391;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 7px;
}

.profile-usertitle-job {
  text-transform: uppercase;
  color: #5b9bd1;
  font-size: 12px;
  font-weight: 600;
  margin-bottom: 15px;
}

.profile-userbuttons {
  text-align: center;
  margin-top: 10px;
}

.profile-userbuttons .btn {
  text-transform: uppercase;
  font-size: 11px;
  font-weight: 600;
  padding: 6px 15px;
  margin-right: 5px;
}

.profile-userbuttons .btn:last-child {
  margin-right: 0px;
}

.profile-usermenu {
  margin-top: 30px;
}

.profile-usermenu ul li {
  border-bottom: 1px solid #f0f4f7;
}

.profile-usermenu ul li:last-child {
  border-bottom: none;
}

.profile-usermenu ul li a {
  color: #93a3b5;
  font-size: 14px;
  font-weight: 400;
}

.profile-usermenu ul li a i {
  margin-right: 8px;
  font-size: 14px;
}

.profile-usermenu ul li a:hover {
  background-color: #fafcfd;
  color: #5b9bd1;
}

.profile-usermenu ul li.router-link-active {
  border-bottom: none;
}

.profile-usermenu ul li a.router-link-active {
  color: #5b9bd1;
  background-color: #f6f9fb;
  border-left: 2px solid #5b9bd1;
  margin-left: -2px;
}
</style>

<script>
  export default {
    name: 'User',
    data () {
      return {
      }
    },
    computed: {
      photo () {
        return this.$store.getters.getUserImage
      },
      displayName () {
        return this.$store.getters.getUser.nickName
      }
    }
  }
</script>



